.LoadingScreen{
    position: fixed;
    background: white;
    width: 100%;
    height:100%;
    z-index: 1000;
    /* display: flex;
    justify-content: center;
    align-self: center; */
}
.lds-ring {
    display: inline-block;
    position: fixed;
    z-index: 2000;
    width: 80px;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    height: 80px;
  }
  .lds-ring div {
    box-sizing: border-box;
    display: block;
    position: absolute;
    width: 64px;
    height: 64px;
    margin: 8px;
    border: 8px solid #ec6b66;
  
    border-radius: 50%;
    animation: lds-ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
    border-color: #ec6b66 transparent transparent transparent;
  }
  .lds-ring div:nth-child(1) {
    animation-delay: -0.45s;
  }
  .lds-ring div:nth-child(2) {
    animation-delay: -0.3s;
  }
  .lds-ring div:nth-child(3) {
    animation-delay: -0.15s;
  }
  @keyframes lds-ring {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(360deg);
    }
  }